.serials-list {
    margin-top: 50px;

    .search-offer {
        display: flex;
        margin-top: 40px;
        align-items: center;
        margin-bottom: 40px;
        justify-content: space-between;

        .title {
            margin-bottom: 0px;
        }

        input {
            width: 100%;
            padding: 10px;
            font-size: 18px;
            max-width: 300px;
        }
    }


    @media screen and (max-width: 576px) {
        .search-offer {
            gap: 10px;
            flex-direction: column;
            align-items: flex-start;

            input {
                margin-left: auto;
            }
        }
    }


    .serials {
        gap: 20px;
        display: flex;
        flex-direction: column;
    }
}